import { Component, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-todo',
  template: `
    <div class="item" [ngClass]="{ complete: todo.completed }">
      <div class="item-label">{{ todo.label }}</div>
      <div class="item-btn-wrap">
        <button type="button" class="btn btn-success btn-sm" (click)="onComplete.emit(todo)">
          <span class="glyphicon glyphicon-ok" aria-hidden="true"></span>
        </button>
        <button type="button" class="btn btn-danger btn-sm" (click)="onRemove.emit(todo)">
          <span class="glyphicon glyphicon-trash" aria-hidden="true"></span>
        </button>
      </div>
    </div>
  `,
  styles: [`
    .item {
      width: 100%;
      height: 40px;
      line-height: 40px;
      border-bottom: 1px solid #cccccc;
      margin-bottom: 20px;
    }
    .complete .item-label {
      text-decoration: line-through;
    }
    .item-label {
      float: left;
      padding-left: 10px;
    }
    .item-btn-wrap {
      float: right;
    }
  `]
})
export class TodoComponent {

  // 부모컴포넌트 todo-list로부터 todo 객체를 전달받는다.
  // todo: {id: number, label: string, completed: boolean }
  @Input() todo;

  // 부모컴포넌트 todo-list와의 통신을 위해
  @Output() onComplete = new EventEmitter();
  @Output() onRemove = new EventEmitter();

}



