import { Component, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-todo-form',
  template: `
    <div class="input-group">
      <input type="text" name="label" [(ngModel)]="label"  class="form-control" placeholder="Todo">
      <span class="input-group-btn">
        <button class="btn btn-info" (click)="onClickAddBtn()"><span class="glyphicon glyphicon-plus" aria-hidden="true"></span></button>
      </span>
    </div>
  `
})
export class TodoFormComponent {

  label: string;

  // 부모컴포넌트 todos와의 통신
  @Output() onAdd = new EventEmitter();

  onClickAddBtn() {
    if(!this.label) return;

    // 부모컴포넌트 todos에게 label을 전달
    this.onAdd.emit(this.label);
    this.label = '';
  }


}



