import { Component } from '@angular/core';

@Component({
  selector: 'app-todos',
  template: `
    <h1>todos</h1>
    <div>
      <app-todo-form (onAdd)="addTodo($event)"></app-todo-form>
      <app-todo-list [todos]="todos" (onComplete)="completeTodo($event)" (onRemove)="removeTodo($event)"></app-todo-list>
    </div>
    <pre *ngIf="(todos)?.length">{{ todos | json }}</pre>
  `,
  styles: [`
    :host {
      display: block;
      max-width: 550px;
      margin: 0 auto;
    }
    :host /deep/ .btn:focus,.btn:active {
      outline: none !important;
    }
    h1 {
      font-size: 80px;
      font-weight: 300;
      text-align: center;
      color: #5bc0de;
      margin-bottom: 30px;
    }
  `]
})
export class TodosComponent {

  // todo: {id: number, label: string, completed: boolean }
  todos: any[];

  addTodo(label: string) {
    if (!this.todos) {
      this.todos = [{ id: 1, label, completed: false }];
    } else {
      this.todos = [{ id: this.todos.length + 1, label, completed: false }, ...this.todos];
    }
  }

  completeTodo(todo) {
    for (let i = 0; i < this.todos.length; i++) {
      if (this.todos[i].id === todo.id) {
        this.todos[i].completed = !this.todos[i].completed;
      }
    }

    // this.todos = this.todos.map(
    //   item => item.id === todo.id ? Object.assign({}, item, { completed: true }) : item
    // );
    console.table(this.todos);
  }
  removeTodo(todo) {
    // Object destructuring
    this.todos = this.todos.filter(({ id }) => id !== todo.id);

    console.table(this.todos);
  }
}



